import { useQuery } from 'react-query';
import axios from 'axios';
import { getCarsApi, getFeaturedCarsApi } from '../api/Api';

const fetchCars = async () => {
  const response = await axios.get(getCarsApi);
  return response.data.results;
};

const fetchFeaturedCars = async () => {
  const response = await axios.get(getFeaturedCarsApi);
  return response.data.results;
};

const getCarDetails = async (car_slug) => {
  const response = await axios.get(`${getCarsApi}/${car_slug}`);
  return response.data;
};

export const useCars = () => {
  return useQuery('cars', fetchCars, {
    staleTime: 5 * 60 * 1000, // 5 minutes
    cacheTime: 10 * 60 * 1000, // 10 minutes
    refetchOnWindowFocus: false,
    onError: (error) => {
      console.error('Error fetching cars:', error);
    }
  });
}; 

export const useFeaturedCars = () => {
  return useQuery('featuredCars', fetchFeaturedCars, {
    staleTime: 5 * 60 * 1000, // 5 minutes
    cacheTime: 10 * 60 * 1000, // 10 minutes
    refetchOnWindowFocus: false,
  });
};

export const useCarDetails = (car_slug) => {
  return useQuery(['carDetails', car_slug], () => getCarDetails(car_slug), {
    staleTime: 5 * 60 * 1000, // 5 minutes
    cacheTime: 10 * 60 * 1000, // 10 minutes
    refetchOnWindowFocus: false,
  });
};