import React, { useEffect, useState } from "react";
import { localRoute } from '../../../routes/localRoutes';
import AOS from "aos";
import "aos/dist/aos.css";
import {  useHistory } from 'react-router-dom';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { useCars, useFeaturedCars } from "../../../hooks/useCars";

export default function ExoticRentalCar() {
    const history = useHistory()
    useEffect(() => {
        window.scrollTo(0, 0)
        AOS.init({
            duration: 2000
        });
    }, [])


    const handleBackCars = () => {
        history.push(localRoute.exoticFleet)
        window.scrollTo(0, 0)
    };

    const handleOpenCarDetails = (car) => {
        history.push(`${localRoute.carDetails}/${car.slug}`)
        window.scrollTo(0, 0)
    };


    const [cars, setCars] = useState([]);
    const { data: carsData, isLoading, isError } = useFeaturedCars();

    useEffect(() => {
        if (carsData) {
            setCars(carsData);
        }
    }, [carsData]);


    return (
        <div class="font-montserrat" data-aos="fade-up" data-aos-duration="1200">

            <div class="container">
                <div class="row">

                    {cars.map((car) => (
                        <div class='col-lg-6 mt-20' >
                        <div>
                            <img 
                            src={car.thumbnail} 
                            alt="Image" 
                            style={{ borderRadius: '5px', cursor: 'pointer' }}
                            onClick={() => handleOpenCarDetails(car)} />

                            <div style={{ marginTop: '10px', display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                                <div style={{ display: 'flex', alignItems: 'center' }}>
                                    <img src={car.logo} alt="Image" style={{ height: '25px' }} />
                                    <span class="font-montserrat fs-10 fw-500" style={{ color: '#fff', marginLeft: '5px' }}>
                                        {car.make} {car.model}
                                    </span>
                                </div>
                                <a style={{ cursor: "pointer", color: '#fff', background: '#B88B2B', borderRadius: '5px' }} class="read-more">
                                    <span style={{ margin: 'auto', fontSize: '15px', fontWeight: '500', padding: '4px 20px 6px', color: '#000' }} 
                                    onClick={() => handleOpenCarDetails(car)}>inquire</span>
                                </a>
                            </div>
                        </div>
                    </div>
                    ))}
                </div>
            </div>
            <div class="text-center mt-40">
                <h2 class="font-montserrat fs-24 fw-600" style={{ color: '#fff' }}>
                    <a onClick={handleBackCars} style={{ cursor: 'pointer' }}>
                        &lt; view all vehicles &gt;
                    </a>
                </h2>
            </div>
        </div>
    )
}
