import React, { useEffect, useState, useContext, } from "react";
import { Helmet } from "react-helmet";
import AOS from "aos";
import "aos/dist/aos.css";
import Contact from 'components/home/contact';
import { useParams } from 'react-router-dom';

import ExoticRental16Car from "components/home/exoticRental16Car";
import { useCarDetails } from "hooks/useCars";


export default function CarDetails() {
    const { car_slug } = useParams();
    
    const { data: carDetails, isLoading, isError } = useCarDetails(car_slug);

    const [car, setCar] = useState(null);
    const [selectedMainImage, setSelectedMainImage] = useState('');
    const [selectedVehicle, setSelectedVehicle] = useState([]);
    const [faqItems, setFaqItems] = useState([]);

    const processHtmlContent = (html) => {
        if (!html) return '';
        // Replace all <p> tags with styled version
        let processedHtml = html.replace(/<p>/g, '<p class="font-montserrat fw-300 fs-16 mt-10" style="color: #fff">');
        // Replace all <h1> tags with styled version
        processedHtml = processedHtml.replace(/<h1>/g, '<h1 class="font-montserrat fs-40 fw-600" style="color: #ffffff; text-align: center">');
        // <h1 style=\"text-align:center\">
        processedHtml = processedHtml.replace(/<h1 style="text-align:center">/g, '<h1 class="font-montserrat fs-30 fw-600" style="color: #ffffff; text-align: center">');
        return processedHtml;
    };

    useEffect(() => {
        if (carDetails) {
            setCar(carDetails);
            setSelectedMainImage(carDetails.main_image);
            setSelectedVehicle(carDetails.images || []);
            setFaqItems([
                {
                    id: 0,
                    question: "how much does it cost to rent a " + carDetails?.make + "?",
                    answer: "at monaco experiences, " + carDetails?.make + " rental prices vary based on the model and rental duration. contact us for specific pricing details.",
                    isCollapsed: true
                },
                {
                    id: 1,
                    question: "why choose a " + carDetails?.make + " in " + carDetails?.color + " color?",
                    answer: "a " + carDetails?.color + " " + carDetails?.make + " stands out for its iconic and vibrant hue, symbolizing power and passion. it's the quintessential choice for making a statement.",
                    isCollapsed: true
                },
                {
                    id: 2,
                    question: "what are the requirements to hire a " + carDetails?.make + "?",
                    answer: "to rent a " + carDetails?.make + ", you must be at least 25 years old, possess a valid driver's license, and carry full insurance coverage. a security deposit is also required at the time of rental.",
                    isCollapsed: true
                },
                {
                    id: 3,
                    question: "how long can i rent a " + carDetails?.make + " for?",
                    answer: carDetails?.make + " rentals at monaco experiences are flexible, ranging from a few hours to several days. custom rental periods can be arranged to suit your needs.",
                    isCollapsed: true
                },
                {
                    id: 4,
                    question: "what happens if the " + carDetails?.make + " is damaged during my rental period?",
                    answer: "in the event of damage, you should immediately report it to us. you  be liable for repair costs up tothe amount of the security deposit, depending on the nature of the damage and your insurance coverage.",
                    isCollapsed: true
                },
                {
                    id: 5,
                    question: "where can i find " + carDetails?.make + " rentals near me?",
                    answer: "monaco experiences provides " + carDetails?.make + " rental services in los angeles, new york, boston, washington dc,new jersey, and more. we ensure our luxury car rental services are easily accessible to meet your needs, no matter where you are. contact us to discuss further.",
                    isCollapsed: true
                }
            ]);
        }
    }, [carDetails]);

    const toggleCollapse = (id) => {
        setFaqItems((prevState) =>
            prevState.map((item) =>
                item.id === id ? { ...item, isCollapsed: !item.isCollapsed } : item
            )
        );
    };

    const handleOpenEnquiry = () => {
        const element = document.querySelector("#enquiry-form");
        if (element) {
            element.scrollIntoView({ behavior: "smooth" });
        }
    };

    if (isLoading) {
        return <div className="container mt-120">Loading...</div>;
    }

    if (isError || !car) {
        return <div className="container mt-120">Error loading car details</div>;
    }

    return (
        <>
            <Helmet>
                <meta charSet="utf-8" />
                <title>
                    Rent A {car.make} - Monaco Experiences - Exotic Car Rental
                </title>
                <meta name="description" content="Enjoy Italian craftsmanship with our Ferrari rentals from Monaco Experiences for your wedding or any special occasion. Contact us for a customised quotation." />
                <meta name="keywords" content="" />
                <link rel="canonical" href="https://monacoexperiences.com/ferrari-f8-rental" />
            </Helmet>


            <div class="container mt-120 md-mt-130">

                <div class="feature-blog-three align-items-center justify-content-between" data-aos="fade-up" data-aos-duration="1200" style={{ display: 'flex', padding: '0px', margin: '0px 5px' }}>
                    <div style={{ display: 'flex' }}>
                        <img src={car.logo} alt="Image" style={{ height: '50px' }} />
                        <div style={{ lineHeight: '25px', marginLeft: '5px' }}>
                            <p>
                                <span class="font-montserrat fs-20 fw-700" style={{ color: '#fff' }}>
                                    {car.make.toLowerCase()} {car.model.toLowerCase()}
                                </span>
                            </p>
                            <p class='font-montserrat fw-500 fs-12' style={{ color: '#fff', lineHeight: 1.2 }}>
                                {car.header}
                            </p>
                        </div>
                    </div>
                    <a style={{ cursor: "pointer", color: '#fff', background: '#B88B2B', borderRadius: '5px', cursor: 'pointer' }} onClick={handleOpenEnquiry} class="read-more">
                        <span class="font-montserrat fs-20" style={{ margin: 'auto', padding: '12px 15px 15px', color: '#000' }}>inquire</span>
                    </a>
                </div>

                <div class="row mt-20 align-items-start justify-content-between" data-aos="fade-up" data-aos-duration="1200">

                    <div class="col-lg-12">
                        <div class=" font-montserrat">

                            <div style={{ cursor: "pointer", display: 'flex', justifyContent: 'center' }} class="post-meta">
                                <img src={selectedMainImage === '' ? selectedVehicle.main_image : selectedMainImage} alt={selectedVehicle.make} style={{ borderRadius: '5px' }} />
                            </div>

                            <div id='div11' class="mt-10" style={{ display: 'flex', justifyContent: 'space-between', overflowY: 'auto' }}>
                                {selectedVehicle && selectedVehicle.map(item =>
                                    <div style={{ cursor: "pointer", margin: '5px' }}>
                                        <img src={item} onClick={() => setSelectedMainImage(item)} style={{ minWidth: '170px', borderRadius: '5px' }} alt={""} />
                                    </div>
                                )}
                            </div>
                        </div>
                    </div>
                </div>

                <div class="row align-items-start justify-content-between mt-20" data-aos="fade-up" data-aos-duration="1200">
                    <div class="col-lg-12">
                        <div className="font-montserrat fw-300 fs-16" style={{ color: '#fff' }}>
                            <div dangerouslySetInnerHTML={{ __html: processHtmlContent(car.description) }} />
                        </div>
                    </div>
                </div>
            </div>

            <div class="font-montserrat feature-blog-one blog-page-bg" data-aos="fade-up" data-aos-duration="1200">
                <div class="container section-heading text-center">
                    <h2 class="font-montserrat fs-40 fw-600" style={{ color: '#ffffff' }}>
                        our ultimate exotic car rental fleet
                    </h2>
                    <p class="font-montserrat fs-20 fw-200" style={{ color: '#ffffff' }}>
                        classy and luxury rental cars  : discover our unique <span style={{ fontWeight: 700 }}>showroom experience</span>
                    </p>
                </div>
                <ExoticRental16Car currentCar={car} />
            </div>

            <div className="mt-30" id="enquiry-form">
                <Contact />
            </div>

            <div style={{ background: '#111' }}>
                <div style={{ background: '#111' }}>
                    <div className="container pt-30 pb-30">
                        <div style={{ textAlign: 'center' }}>
                            <h2 className="font-montserrat fs-40 fw-600" style={{ color: '#FFF' }}>
                                frequently asked questions
                            </h2>
                            <p className="font-montserrat mt-10" style={{ color: '#BABABA' }}>
                                these are the questions we've received from our real-time customers about renting an exotic car from monaco experiences.
                            </p>
                        </div>

                        {faqItems.map((item) => (
                            <div key={item.id} className="container mt-40">
                                <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                                    <h3
                                        className="font-montserrat fs-20 fw-500"
                                        onClick={() => toggleCollapse(item.id)}
                                        style={{ color: '#BABABA', cursor: 'pointer' }}
                                    >
                                        {item.question}
                                    </h3>
                                    <h3
                                        className="font-montserrat"
                                        onClick={() => toggleCollapse(item.id)}
                                        style={{ color: '#BABABA', cursor: 'pointer', marginRight: '10px' }}
                                    >
                                        {item.isCollapsed ? <>&#9660;</> : <>&#9650;</>}
                                    </h3>
                                </div>
                                <hr style={{ borderColor: '#BABABA' }} />
                                {!item.isCollapsed && (
                                    <p className="font-montserrat fs-16 fw-300" style={{ color: '#fff' }}>
                                        {item.answer}
                                    </p>
                                )}
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </>
    )
}